.up-wrapper {
  background: whitesmoke;
  min-height: 100vh;
  .up-header {
    position: fixed;
    height: 60px;
    background: white;
    top: 0;
    left: 0;
    right: 0;
    box-shadow: 0px 0px 8px 2px lightgray;
    display: flex;
    padding: 0 3%;
    align-items: center;
    z-index: 100;
    .btn {
      i {
        margin-right: 10px;
      }
    }
  }
  .up-content {
    padding-top: 80px;
    padding-bottom: 30px;
    main {
      width: 768px;
      margin: auto;
      .up-content-title {
        font-size: 25px;
        font-weight: 500;
        margin: 10px 0;
        color: gray;
      }
      .up-categoryBox {
        height: 100px;
        box-shadow: 0 0 3px lightgray;
        margin-bottom: 30px;
        background: white;
        border-radius: 3px;
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding: 0 5%;
        span {
          font-size: 20px;
          font-weight: 500;
        }
      }
      .up-formInputs {
        border-radius: 3px;
        background: white;
        box-shadow: 0 0 3px lightgray;
        padding: 4%;
        .rowItem {
          margin: 20px 0;
        }
    
        .actions {
          display: flex;
          flex-direction: row-reverse;
        }
        .shortDesc textarea {
          min-height: 100px;
        }
        .longDesc textarea {
          min-height: 200px;
        }
      }
    }
  }
}
